require("./../vendor/angular/angular.js");
require("./../vendor/angular-route/angular-route.js");
require("./../vendor/angular-animate/angular-animate.js");
require("./../vendor/angular-bootstrap/ui-bootstrap-tpls.js");
require("./../vendor/angular-cookies/angular-cookies.js");
require("./../vendor/angular-dynamic-locale/src/tmhDynamicLocale.js");
require("./../vendor/angular-resource/angular-resource.js");
require("./../vendor/angular-sanitize/angular-sanitize.js");
require("./../vendor/angular-translate/angular-translate.js");
require("./../vendor/angular-translate-handler-log/angular-translate-handler-log.js");
require("./../vendor/angular-translate-loader-static-files/angular-translate-loader-static-files.js");
require("./../vendor/angular-translate-storage-cookie/angular-translate-storage-cookie.js");
require("./../vendor/angular-translate-storage-local/angular-translate-storage-local.js");
require("./../vendor/ngGeolocation/ngGeolocation.js");
require("./../vendor/ngInfiniteScroll/build/ng-infinite-scroll.js");

require('../dist/partials/templateCachePartials');
require('./services');
require('./animations');
require('./directives');
require('./controllers');

var wirknApp = angular.module('wirknApp',[
    'ngRoute',
    'ngGeolocation',
    'ngCookies',
    'ngSanitize',
    'pascalprecht.translate',
    'tmh.dynamicLocale',
    'infinite-scroll',
    'ui.bootstrap',
    'wirknPartials',
    'wirknAnimations',
    'wirknControllers',
    'wirknDirectives',
    'wirknServices'
   ])
    .constant('LOCALES', {
        'locales': {
            'en': 'English',
            'fr': 'French',
            'ru': 'Russian'
        },
        'preferredLocale': 'en'
    });

wirknApp.config(['$routeProvider', 'LOCALES', function($routeProvider, LOCALES){
    $routeProvider.
    when('/:locale/list/:searchTerm?', {
        templateUrl: '/partials/list.html',
        controller: 'ListController'
    }).
    when('/:locale/details/:itemId', {
        templateUrl: '/partials/details.html',
        controller: 'DetailsController'
    }).
    when('/:locale/search', {
        templateUrl: '/partials/search.html',
        controller: 'SearchController'
    }).
    when('/:locale/location', {
        templateUrl: '/partials/location.html',
        controller: 'LocationController'
    }).
    otherwise({
        redirectTo: '/' + LOCALES.preferredLocale + '/list'
    });
}]);

wirknApp.config(['$translateProvider', function($translateProvider) {
    $translateProvider.useMissingTranslationHandlerLog();
}]);

wirknApp.config(['$translateProvider', function($translateProvider) {
    $translateProvider.useStaticFilesLoader({
        prefix: 'resources/locale-',
        suffix: '.json'
    });
    $translateProvider.preferredLanguage('en');
    $translateProvider.useLocalStorage();
}]);

wirknApp.config(['tmhDynamicLocaleProvider', function(tmhDynamicLocaleProvider) {
    tmhDynamicLocaleProvider.localeLocationPattern('vendor/angular-i18n/angular-locale_{{locale}}.js');
}]);

